.fullscreen-menu .menu-item > a > .imagearrow {
  transition: margin-left 0.5s ease-in-out;

  width: 8%;
  height: 5%;
  margin-top: 1.8%;
  margin-right: -3.8%;
  text-align: left;
  overflow: hidden;
  opacity: 0;
  clip-path: polygon(
    100% 0,
    100% 100%,
    100% 100%,
    100% 0
  ); /* Initial clip-path to show only the right side */
  transform: translateX(-100%); /* Initial state: move the image to the right */
  transition: opacity 0.5s ease-in-out, clip-path 0.5s ease-in-out,
    transform 0.5s ease-in-out;
}

.menu-item:hover a .imagearrow {
  margin-left: 2%;
  opacity: 1;
  clip-path: polygon(
    0 0,
    100% 0,
    100% 100%,
    0 100%
  ); /* Final clip-path to reveal the whole image */
  transform: translateX(5%);
}
