.showcssline {
  animation-name: drawLine;
  animation-duration: 2s; /* Adjust the time as needed */
  animation-fill-mode: forwards; /* Keeps the state of the animation at the end */
  animation-timing-function: linear; /* Ensures the animation speed is consistent */
}

@keyframes drawLine {
  from {
    height: 0%;
  }
  to {
    height: 100%;
  }
}

